export default ({title, imgSrc, imgSrcHover}) => (
    <div className="bg-white hover:bg-primary text-black hover:text-white border-r-8 flex-column p-3">
        <h5
            className=""
        >{title}</h5>
        <img 
            src={imgSrc}
            className="ml-auto hover:hidden w-32 h-32"
        />
        <img 
            src={imgSrcHover}
            className="ml-auto hidden hover:block w-32 h-32"
        />
    </div>
)